<template>
  <div>
    <mu-appbar style="width: 100%;" :color="$store.state.color" class="header">
      <a @click="toBack()">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span style="font-size:16px;">意见反馈</span>
    </mu-appbar>
    <div class="contentFeedback" :style="height">
      <div class="back">
        <div class="imageSizeTitle">
          <div class="imageSize">
            <img src="static/img/feedBack.png" alt />
          </div>
          <div class="title">
            <span>反馈主题</span>
          </div>
        </div>
        <div class="inputDiv">
          <input type="text" class="input" placeholder="请输入反馈主题" v-model="title" />
        </div>
        <div class="inputDiv">
          <input type="text" class="input" placeholder="反馈人" maxlength="5" v-model="person" />
        </div>
        <div class="inputDiv">
          <input
            type="text"
            class="input"
            oninput="value=value.replace(/[^\d]/g,'')"
            placeholder="联系方式(限制11位手机号码)"
            maxlength="11"
            v-model="phone"
          />
        </div>
        <div class="imageSizeTitle">
          <div class="imageSize">
            <img src="static/img/feedBack.png" alt />
          </div>
          <div class="title">
            <span>反馈意见</span>
          </div>
        </div>
        <div>
          <textarea name id="textContent" cols="30" rows="10" v-model="content"></textarea>
        </div>
        <div class="advice">
          <span>欢迎您对我们提出宝贵意见和建议，您留下的信息都将用于软件的升级与改善。</span>
        </div>
      </div>
      <div class="bottom">
        <div class="btn" :style="'background:'+$store.state.color" @click="confirmSubmit()">
          <span>提交</span>
        </div>
      </div>
    </div>

    <mu-container>
      <mu-dialog
        title="确认提交？"
        width="300"
        max-width="80%"
        :esc-press-close="false"
        :overlay-close="false"
        :open.sync="openAlert"
      >
        数据提交之后，我们会尽快查阅您的反馈信息，然后采纳并更新我们的软件！
        <mu-button slot="actions" flat color="primary" @click="closeAlertDialog">取消</mu-button>
        <mu-button slot="actions" flat color="primary" @click="submit">提交</mu-button>
      </mu-dialog>
    </mu-container>
  </div>
</template>
<script type="text/ecmascript-6">
import AppBace from "../../../static/ajax";
import Vue from "vue";
import { Toast } from "vant";
Vue.use(Toast);
import { Dialog } from "vant";
import "vant/lib/dialog/style";
export default {
  name: "cameras-and-albums",
  data() {
    return {
      height: "",
      token: "",
      title: "",
      person: "",
      phone: "",
      content: "",
      openAlert: false
    };
  },
  methods: {
    // 提示框
    confirmSubmit() {
      var _this = this;
      Dialog.confirm({
        title: "请确认是否提交数据？",
        message: "提交人员为：" + this.person
      })
        .then(() => {
          // on confirm
          _this.submit();
        })
        .catch(() => {
          // on cancel
          Toast("您点击了取消");
        });
    },
    toBack() {
      this.$router.go(-1);
    },
    async submit() {
      this.openAlert = false;
      var url = "/app";
      var title = this.title;
      var person = this.person;
      var phone = this.phone;
      var content = this.content;
      var type = 0;
      var res = await AppBace.request(
        url,
        "POST",
        {
          title,
          person,
          phone,
          content,
          type
        },
        this.token
      );
      console.log(res);
      if (res.data.message === "success") {
        // this.$router.push({
        //   path:"/Navigation",query:{
        //     id:"3"
        //   }
        // });
        this.$router.go(-1);
      } else {
        Toast("信息提交失败");
      }
    },
    closeAlertDialog() {
      this.openAlert = false;
      Toast("信息提交失败");
    },
    openAlertDialog() {
      this.openAlert = true;
    }
  },
  created() {},
  mounted() {
    this.token = sessionStorage.getItem("token");
    console.log(document.documentElement.clientHeight);
    this.height =
      "height:" + (document.documentElement.clientHeight - 58) + "px";
    console.log(this.height);
  }
};
</script>
<style scoped>
@import "../../../static/css/model.css";
#textContent {
  width: 100%;
  height: 200px;
  border-radius: 10px;
  border: 1px solid #dcdcdc;
  outline: none;
  color: #7e7e7e;
}
.contentFeedback {
  padding: 10px;
  background: #ececec;
  box-shadow: 2px 2px 2px #ccc;

  /* text-align: center; */
}
.input {
  border: 1px solid #dcdcdc;
  border-radius: 5px;
  width: 100%;
  height: 30px;
  padding: 0 5px;
  outline: none;
  color: #7e7e7e;
}
.back {
  padding: 10px;
  background: white;
  border-radius: 10px;
}
.btn {
  background: #46c47d;
  height: 40px;
  color: white;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 96%;
}
.bottom {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}
.advice {
  text-align: center;
  font-size: 14px;
  color: #7e7e7e;
  height: 60px;
  margin-top: 10px;
}
.title {
  color: #f0c03d;
  font-size: 14px;
  margin-left: 5px;
}
.imageSize img {
  width: 20px;
  height: 20px;
}
.imageSize {
  display: flex;
  align-items: center;
}
.imageSizeTitle {
  display: flex;
  flex-direction: row;
  height: 40px;
  align-items: center;
  /* margin-left: 10px; */
}
.inputDiv {
  margin-bottom: 10px;
}
</style>




